import Manufacturers from '../Containers/Manufacturers'
import Tovars from '../Containers/Tovars'
import Providers from '../Containers/Providers'
import Type from '../Containers/Type'
import People from '../Containers/People'
import Storage from '../../Universal/Containers/Storage'
import StatisticsArrival from '../Containers/Arrival'
import StatisticsReal from '../Containers/Real'
import Statistics from '../../Universal/Containers/Statistics'


import {Router, Route} from 'react-router-dom'
import {createBrowserHistory} from 'history'
import React, {Component} from 'react';
import Menu from './Menu/Menu'

const history = createBrowserHistory();

class App extends Component {
	render(){
		return (
			<div>
				<Router history={history}>
					<Menu socket={this.props.socket}/>
					<div>
						<Route render={ () => <Tovars socket={this.props.socket}/>} exact path='/app/tovars'/>
						<Route render={ () => <Manufacturers socket={this.props.socket}/>} exact path='/app/manufacturers'/>
						<Route render={ () => <Providers socket={this.props.socket}/>} exact path='/app/providers'/>
						<Route render={ () => <Type socket={this.props.socket}/>} exact path='/app/type'/>
						<Route render={ () => <People socket={this.props.socket}/>} exact path='/app/people'/>
						<Route render={ () => <Storage socket={this.props.socket}/>} exact path='/app/storage'/>
						<Route render={ () => <StatisticsReal socket={this.props.socket}/>} exact path='/app/statistics/real'/>
						<Route render={ () => <StatisticsArrival socket={this.props.socket}/>} exact path='/app/statistics/arrival'/>
						<Route render={ () => <Statistics socket={this.props.socket}/>} exact path='/app/statistics'/>
					</div>
				</Router>
			</div>
		)
	}
}

export default App;
