import {Router, Route, Redirect} from 'react-router-dom'
import {createBrowserHistory} from 'history'
import React, {Component} from 'react';
import Statistics from '../../Universal/Containers/Statistics'

import Menu from './Menu/Menu'
import Arrival from '../Containers/Arrival'
import Real from '../Containers/Real'
import Storage from '../../Universal/Containers/Storage'

const history = createBrowserHistory();

class App extends Component {
	render(){
		return (
			<div>
				<Router history={history}>
					<Menu socket={this.props.socket}/>
					<div>
						<Route render={ () => <Arrival socket={this.props.socket}/>} exact path='/app/arrival'/>
						<Route render={ () => <Real socket={this.props.socket}/>} exact path='/app/real'/>
						<Route render={ () => <Storage socket={this.props.socket}/>} exact path='/app/storage'/>
					</div>
					<Route render={ () => <Statistics socket={this.props.socket}/>} exact path='/app/statistics'/>
				</Router>
			</div>
		)
	}
}

export default App;
