const initialState = {
	flag: false,
	obj: '',
	manufacturer: {
		Id: 0,
		Name: ''
	},
	type: {
		Id: 0,
		Name: ''
	},
	tovars: {
		Id: 0,
		Name: ''
	},
	people: {
		Id: 0,
		Name: ''
	},
	providers: {
		Id: 0,
		Name: ''
	},
	storage: {
		Id: 0,
		Name: ''
	}

};

export default ( state = initialState, action ) => {
	switch (action.type) {
		case 'SET_ITEMS':
			return {
				...state,
				obj: action.payload.obj,
				flag: action.payload.flag
			};
		case 'SET_SELECT_ID_MANUFACTURER':
			return {
				...state,
				manufacturer: {
					Id: action.payload.Id,
					Name: action.payload.Name
				}
			}
		case 'SET_SELECT_ID_TYPE':
			return {
				...state,
				type: {
					Id: action.payload.Id,
					Name: action.payload.Name
				}
			}
		case 'SET_SELECT_ID_TOVARS':
			return {
				...state,
				tovars: {
					Id: action.payload.Id,
					Name: action.payload.Name
				}
			}
		case 'SET_SELECT_ID_PEOPLE':
			return {
				...state,
				people: {
					Id: action.payload.Id,
					Name: action.payload.Name
				}
			}
		case 'SET_SELECT_ID_PROVIDERS':
			return {
				...state,
				providers: {
					Id: action.payload.Id,
					Name: action.payload.Name
				}
			}
		case 'SET_SELECT_ID_STORAGE':
			return {
				...state,
				storage: {
					Id: action.payload.Id,
					Name: action.payload.Name
				}
			}
		default:
			return state;
	}
}