import React, {Component} from 'react';
import s from './Storage.module.css'
import ReactToPrint from "react-to-print";

class Storage extends Component{

	constructor(props) {
		super(props);
		this.state = {
			id: 0,
			manufacturer: '',
			type: '',
			items: []
		};
	}
	componentWillMount() {
		if (!this.props.items.flag) {
			this.props.socket.emit('getInformation', 'Storage');
			console.log('text')
		}
		this.props.socket.on('resInformationStorage', (data, obj) => {
			console.log(this.props)
			this.setState({items: data})
			const { setItems } = this.props;
			setItems({obj ,flag: true})
		});

		this.props.socket.on('resStorageFindOne', (res) => {
			this.setState({items: res})
			const { setItems } = this.props;
			setItems({obj: 'Storage' ,flag: true})
		})
	}
	componentWillUnmount() {
		this.props.socket.removeListener('resInformationStorage')
		this.props.socket.removeListener('resStorageFindOne')

	}

	handleSubmitFind = (e) =>{
		e.preventDefault();

		this.props.socket.emit('storageFindOne', this.refs.Find.value);
	}
	handlePrint = () => {
		
	}
	handleChange = () => {
	}
	render(){
		return(
			<div>
				<div className={s.findPanel}>
					<form onSubmit={this.handleSubmitFind}>
						<div className={s.findLine}>
							<input ref="Find" type="text" placeholder="Поиск" />
							<button type="submit">Поиск</button>
							<ReactToPrint
								trigger={() => <button type="button" onClick={this.handlePrint}>Печать</button>}
								content={() => this.componentRef}
							/>
						</div>
					</form>
				</div>

				<div ref={el => (this.componentRef = el)} className={s.table}>
					<div className={s.bottomPrint}>
						<div>Отчет со склада</div>
					</div>
					<div className={s.tTitle}>
						<div>Артикул</div>
						<div>Наименование</div>
						<div>Цена</div>
						<div>Ед. изм</div>
						<div>Производитель</div>
						<div>Категория</div>
						<div>Зарезерворованное</div>
						<div>Кол-во</div>
						<div>Итого</div>
					</div>
					<div className={s.tBody}>
						{this.props.items.obj === 'Storage' ? (function(){
							return this.state.items.map((item, i) => {
								return <div className={s.tBodyItems} key={i}>
									<div>{item.Number}</div>
									<div>{item.Name}</div>
									<div>{item.Price}</div>
									<div>{item.Unit}</div>
									<div>{item.NameMF}</div>
									<div>{item.NameT}</div>
									<div>{item.reserved}</div>
									<div>{item.Quantity}</div>
									<div>{item.Count}</div>
									<div>{item.Id}</div>
								</div>
							})
						}.bind(this))() : 'Загрузка...'}
					</div>
					<div className={s.bottomPrint}>
						<div>Сформировал: {localStorage.Name}</div>
					</div>
				</div>
			</div>
		)
	}
}

export default Storage;
