const initialState = {
	items: [],
	textFlag: null,
	openModalType: false,
	openModalManufacturer: false,
	openModalTovars: false,
	openModalPeople: false,
	openModalProviders: false,
	openModalStorage: false
};

export default ( state = initialState, action ) => {
	switch (action.type) {
		case 'SET_MODALITEMS':
			return {
				...state,
				items: action.payload.data,
				objFlag: action.payload.text
			};
		case 'SET_OPEN_MODAL_MANUFACTURER':
			return {
				...state,
				openModalManufacturer: action.payload.flag
			}
		case 'SET_OPEN_MODAL_TYPE':
			return {
				...state,
				openModalType: action.payload.flag
			}
		case 'SET_OPEN_MODAL_TOVARS':
			return {
				...state,
				openModalTovars: action.payload.flag
			}
		case 'SET_OPEN_MODAL_PEOPLE':
			return {
				...state,
				openModalPeople: action.payload.flag
			}
		case 'SET_OPEN_MODAL_PROVIDERS':
			return {
				...state,
				openModalProviders: action.payload.flag
			}
		case 'SET_OPEN_MODAL_STORAGE':
			return {
				...state,
				openModalStorage: action.payload.flag
			}
		default:
			return state;
	}
}