import React, {Component} from 'react';
import s from './Tovars.module.css'
import ManufacturersModalDisplay from '../../ModalContainers/Manufacturers'
import TypeModalDisplay from '../../ModalContainers/Type'


class Tovars extends Component{

	constructor(props) {
		super(props);
		this.state = {
			id: 0,
			manufacturer: '',
			type: '',
			items: []
		};
		this.handleClickBodyItems = this.handleClickBodyItems.bind(this)
	}
	componentWillMount() {
		if (!this.props.items.flag) {
			this.props.socket.emit('getInformation', 'Tovars');
			console.log('text')
		}
		this.props.socket.on('resInformationTovars', (data, obj) => {
			console.log('text')
			this.setState({items: data})
			const { setItems } = this.props;
			setItems({obj ,flag: true})
		});

	}
	componentWillUnmount() {
		this.props.socket.removeListener('resInformationTovars')
	}
	componentDidMount() {
		this.props.setSelectIdManufacturer({Id: 0, Name: ''})
		this.props.setSelectIdType({Id: 0, Name: ''})
	}


	componentWillReceiveProps(nextProps) {
		console.log(nextProps)
		if (nextProps.items.type.Id) {
			this.setState({type: nextProps.items.type.Name})
		}
		if (nextProps.items.manufacturer.Id) {
			this.setState({manufacturer: nextProps.items.manufacturer.Name})
		}
	}

	handleClickBodyItems = (e) => {
		e.persist() 
		var i, j;
		if(e.target.parentElement.className === s.tBody) {
			for (i = 0; i < e.target.parentElement.children.length; i++) {
				for (j = 0; j < e.target.parentElement.children[i].children.length; j++) {
					e.target.parentElement.children[i].children[j].classList.remove(s.active);
				}
			}
		} else {
			for (i = 0; i < e.target.parentElement.parentElement.children.length; i++) {
				for (j = 0; j < e.target.parentElement.parentElement.children[i].children.length; j++) {
					e.target.parentElement.parentElement.children[i].children[j].classList.remove(s.active);
				}
			}

		}


		if (e.target.className === s.tBodyItems) {
			for (i = 0; i < e.target.children.length; i++) {
				e.target.children[i].className = s.active;
			}
			this.setState({id: e.target.children[e.target.children.length - 1].innerText})

		} else {
			for (i = 0; i < e.target.parentElement.children.length; i++) {
				e.target.parentElement.children[i].className = s.active;
			}
			this.setState({id: e.target.parentElement.children[e.target.parentElement.children.length - 1].innerText})
		}

	}
	handleClickSubmitAdd = (e) =>{
		e.preventDefault();
		e.persist() 

		let refs = this.refs

		let data = {
			Number: refs.Number.value,
			Name: refs.Name.value,
			Price: refs.Price.value,
			Unit: refs.Unit.value,
			ManufacturerId: this.props.items.manufacturer.Id,
			TypeId: this.props.items.type.Id
		}
		this.props.socket.emit('insertInTo', 'Tovars', data);

	}
	handleClickUpdate = () => {
		let refs = this.refs

		let data = {
			Number: refs.Number.value,
			Name: refs.Name.value,
			Price: refs.Price.value,
			Unit: refs.Unit.value,
			ManufacturerId: this.props.items.manufacturer.Id,
			TypeId: this.props.items.type.Id,
			Id: this.state.id
		}

		this.props.socket.emit('update', 'Tovars', data);

	}
	handleClickDelete = () => {
		this.props.socket.emit('deleteFrom', 'Tovars', this.state.id);
	}

	openModalDisplayManufacturers = () => {
		this.props.socket.emit('getInformationModal', 'Manufacturers');
		this.props.setOpenModalManufacturer({flag: true});
	}

	openModalDisplayType = () => {
		this.props.socket.emit('getInformationModal', 'Type');
		this.props.setOpenModalType({flag: true});
	}
	handleChange = () => {
		console.log('tnk')
	}
	render(){
		return(
			<div>
				<div className={s.table}>
					<div className={s.tTitle}>
						<div>Артикул</div>
						<div>Наименование</div>
						<div>Цена</div>
						<div>Ед. изм</div>
						<div>Производитель</div>
						<div>Категория</div>
					</div>
					<div className={s.tBody}>
						{this.props.items.obj === 'Tovars' ? (function(){
							return this.state.items.map((item, i) => {
								return <div className={s.tBodyItems} onClick={this.handleClickBodyItems} key={i}>
									<div>{item.Number}</div>
									<div>{item.Name}</div>
									<div>{item.Price}</div>
									<div>{item.Unit}</div>
									<div>{item.NameMF}</div>
									<div>{item.NameT}</div>
									<div>{item.Id}</div>
								</div>
							})
						}.bind(this))() : 'Загрузка...'}
					</div>
				</div>
				<div className={s.editPanel}>
					<form onSubmit={this.handleClickSubmitAdd}>
						<div className={s.line}>
							<input ref="Number" type="text" placeholder="Артикул" />
							<input ref="Name" type="text" placeholder="Наименование" />
							<input ref="Price" type="text" placeholder="Цена" />
						</div>
						<div className={s.line}>
							<input ref="Unit" type="text" placeholder="Единица измерения" />
							<div>
								<input ref="Manufacturer" onChange={this.handleChange} type="text" placeholder="Производитель" value={this.state.manufacturer}/>
								<button type="button" onClick={this.openModalDisplayManufacturers}></button>
							</div>
							<div>
								<input ref="Type" onChange={this.handleChange} type="text" placeholder="Категория" value={this.state.type} />
								<button type="button" onClick={this.openModalDisplayType}></button>
							</div>
						</div>
						<div className={s.lineButtons}>
							<button type="submit">Добавить</button>
							<button type="button" onClick={this.handleClickDelete}>Удалить</button>
							<button type="button" onClick={this.handleClickUpdate}>Обновить</button>
						</div>
					</form>
				</div>
				{this.props.modal.openModalType ? <TypeModalDisplay showModalType={this.props.modal.openModalType} socket={this.props.socket}/> : null}
				{this.props.modal.openModalManufacturer ? <ManufacturersModalDisplay showModal={this.props.modal.openModalManufacturer} socket={this.props.socket}/> : null}
			</div>
		)
	}
}

export default Tovars;
