import React, {Component} from 'react';
import s from './Providers.module.css'


class Providers extends Component{

	constructor(props) {
		super(props);
		this.state = {
			Id: 0,
			items: []
		};
		this.handleClick = this.handleClick.bind(this);
	}
	componentWillMount() {
		if (!this.props.items.flag) {
			this.props.socket.emit('getInformation', 'Providers');
		}
		this.props.socket.on('resInformationProviders', (data, obj) => {
			const { setItems } = this.props;
			this.setState({items: data})
			setItems({obj ,flag: true})
		});
	}
	componentWillUnmount() {
		this.props.socket.removeListener('resInformationProviders')
	}
	handleClick = (e) => {
		e.persist()
		var i, j;
		console.log('nkjmqkn')
		if(e.target.parentElement.className === s.tBody) {
			for (i = 0; i < e.target.parentElement.children.length; i++) {
				for (j = 0; j < e.target.parentElement.children[i].children.length; j++) {
					e.target.parentElement.children[i].children[j].classList.remove(s.active);
				}
			}
		} else {
			for (i = 0; i < e.target.parentElement.parentElement.children.length; i++) {
				for (j = 0; j < e.target.parentElement.parentElement.children[i].children.length; j++) {
					e.target.parentElement.parentElement.children[i].children[j].classList.remove(s.active);
				}
			}
		}
		if (e.target.className === s.tBodyItems) {
			for (i = 0; i < e.target.children.length; i++) {
				e.target.children[i].className = s.active;
				this.setState({Id: e.target.children[e.target.children.length - 1].innerText})
			}
		} else {
			for (i = 0; i < e.target.parentElement.children.length; i++) {
				e.target.parentElement.children[i].className = s.active;
				this.setState({Id: e.target.parentElement.children[e.target.parentElement.children.length - 1].innerText})
			}
		}
	}
	handleSubmitAdd = (e) => {
		e.preventDefault();
		var data = {
			Name: this.refs.Name.value,
			Address: this.refs.Address.value,
			Tel: this.refs.Tel.value,
			Email: this.refs.Email.value,
			Date: this.refs.Date.value,
			INN: this.refs.INN.value
		}
		if (data.Name && data.Address && data.Tel && data.Email && data.Date && data.INN){
			this.props.socket.emit('insertInTo','Providers',data);
		}
	}
	handleClickDelete = () => {

		if (this.state.Id) {
			this.props.socket.emit('deleteFrom','Providers',this.state.Id);
		}
	}
	handleClickUpdate = () => {
		var data = {
			Name: this.refs.Name.value,
			Address: this.refs.Address.value,
			Tel: this.refs.Tel.value,
			Email: this.refs.Email.value,
			Date: this.refs.Date.value,
			INN: this.refs.INN.value,
			Id: this.state.Id
		}
		console.log(data)
		if (data.Name && data.Address && data.Tel && data.Email && data.Date && data.INN && data.Id){
			this.props.socket.emit('update','Providers',data)
		}
	}

	render(){
		return(
			<div>
				<div className={s.table}>
					<div className={s.tTitle}>
						<div>Наименование</div>
						<div>Адрес</div>
						<div>Телефон</div>
						<div>Email</div>
						<div>Дата основания</div>
						<div>ИНН</div>
					</div>
					<div className={s.tBody}>
						{this.props.items.obj === 'Providers' ? (function(){
							return this.state.items.map((item, i) => {
								return <div className={s.tBodyItems} onClick={this.handleClick} key={i}>
									<div>{item.Name}</div>
									<div>{item.Address}</div>
									<div>{item.Tel}</div>
									<div>{item.Email}</div>
									<div>{item.Date}</div>
									<div>{item.INN}</div>
									<div>{item.Id}</div>
								</div>
							})
						}.bind(this))() : 'Загрузка...'}
					</div>
				</div>
				<div className={s.editPanel}>
					<form onSubmit={this.handleSubmitAdd}>
						<div className={s.line}>
							<input ref="Name" type="text" placeholder="Наименование"/>
							<input ref="Address" type="text" placeholder='Адрес'/>
							<input ref="Tel" type="text" placeholder='Телефон'/>
						</div>
						<div className={s.line}>
							<input ref="Email" type="text" placeholder='Email'/>
							<input ref="Date" type="text" placeholder='Дата основания'/>
							<input ref="INN" type="text" placeholder='ИНН'/>
						</div>
						<div className={s.lineButtons}>
							<button type="submit">Добавить</button>
							<button type="button" onClick={this.handleClickDelete}>Удалить</button>
							<button type="button" onClick={this.handleClickUpdate}>Обновить</button>
						</div>
					</form>
				</div>
			</div>
		)
	}
}

export default Providers;
