import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as actions from '../../Actions/items';
import * as actionsModal from '../../Actions/modal'
import Real from '../Components/Real/Real';


const mapStateProps = ({items, modal}) => ({
	items,
	modal
})

const mapDispatchToProps = dispatch => ({
	...bindActionCreators(actions, dispatch),
	...bindActionCreators(actionsModal, dispatch)
});


export default connect(mapStateProps, mapDispatchToProps)(Real);
