import Admin from './Admin/Containers/App'
import User from './User/Containers/App'

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as actionsAuth from './Actions/auth'

import {Route, Redirect} from 'react-router-dom'
import React, {Component} from 'react';

class MainComponent extends Component {
	constructor(props) {
		super(props);
		this.state = {
			check: false,
			checkRes: false
		};
	}
	componentWillReceiveProps(nextProps) {
		this.setState({checkRes: true})
	}
	render(){
		const {res, account} = this.props.auth;
		return (
			<div>
			{!this.state.checkRes ? null : !res ? (<Redirect to='/' />) : account === 'admin' ?
				(<Route render={ () => <Admin socket={this.props.socket}/>} exact path='/app/*'/>)
				: account === 'user' ? <Route render={() => <User socket={this.props.socket} />} path='/app/*' /> : (<Redirect to='/' />)
			}
			</div>
		)
	}
}

const mapStateProps = ({items, auth}) => ({
	auth
})

const mapDispatchToProps = dispatch => ({
	...bindActionCreators(actionsAuth, dispatch)
});

export default connect(mapStateProps, mapDispatchToProps)(MainComponent);
