import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as actions from '../../Actions/items';
import * as setOpenModal from '../../Actions/modal'
import Tovars from '../Components/Tovars/Tovars';


const mapStateProps = ({items, modal}) => ({
	items,
	modal
})

const mapDispatchToProps = dispatch => ({
	...bindActionCreators(actions, dispatch),
	...bindActionCreators(setOpenModal, dispatch)
});


export default connect(mapStateProps, mapDispatchToProps)(Tovars);
