import React, {Component} from 'react';
import s from './Menu.module.css'
import {Link} from 'react-router-dom'


class Menu extends Component{
	handleClick = (obj,text) => {
		this.props.socket.removeListener('resInformation')
		this.props.socket.emit('getInformation',obj, localStorage.Id);
	}
	render(){
		return(
			<ul className={s.mainUl}>
				<li>
					<Link to='/app/storage'>
						<div onClick={this.handleClick.bind(this, 'Storage')}>
							Склад
						</div>
					</Link>
				</li>
				<li>
					<div>Управление складом</div>
					<ul className={s.subUl}>
						<li><Link to='/app/arrival'><div onClick={this.handleClick.bind(this, 'Arrival')}>Приход товаров</div></Link></li>
						<li><Link to='/app/real'><div onClick={this.handleClick.bind(this, 'Real')}>Расход товаров</div></Link></li>
					</ul>
				</li>
				<li>
					<Link to='/app/statistics'>
						<div>
							Статистика
						</div>
					</Link>
				</li>
				<li><div>Portfolio</div></li>
				<li><div>Contact</div></li>
			</ul>
		)
	}
}

export default Menu;
