import React, {Component} from 'react';
import s from './People.module.css'


class PeopleModalDisplay extends Component{

	constructor(props) {
		super(props);
		this.state = {
			id: 0,
			Name: ''
		};
	}
	
	componentWillMount() {
		this.props.socket.on('resInformationModal', (data, obj) => {
			const { setModalItems } = this.props;
			setModalItems({data,obj})
		});
	}
	componentWillUnmount() {
		this.props.socket.removeListener('resInformationModal')
	}

	componentWillReceiveProps(nextProps) {
		if (nextProps.showModal) {
			if (nextProps.modal.openModalPeople){
				this.refs.overlay.style.display = 'block'
				this.refs.popup.style.display = 'block'
			}
		}
		
	}
	
	handleClickClose = (e) => {
		this.refs.overlay.style.display = 'none'
		this.refs.popup.style.display = 'none'

		this.props.setOpenModalPeople({flag: false});
	}
	handleClickTBodyItem = (e) => {
		e.persist() 
		var i, j;
		if(e.target.parentElement.className === s.tBody) {
			for (i = 0; i < e.target.parentElement.children.length; i++) {
				for (j = 0; j < e.target.parentElement.children[i].children.length; j++) {
					e.target.parentElement.children[i].children[j].classList.remove(s.active);
				}
			}
		} else {
			for (i = 0; i < e.target.parentElement.parentElement.children.length; i++) {
				for (j = 0; j < e.target.parentElement.parentElement.children[i].children.length; j++) {
					e.target.parentElement.parentElement.children[i].children[j].classList.remove(s.active);
				}
			}
		}
		if (e.target.className === s.tBodyItems) {
			for (i = 0; i < e.target.children.length; i++) {
				e.target.children[i].className = s.active;
			}
			this.setState({id: e.target.children[e.target.children.length - 1].innerText, Name: e.target.children[0].innerText})
		} else {
			for (i = 0; i < e.target.parentElement.children.length; i++) {
				e.target.parentElement.children[i].className = s.active;
			}
			this.setState({id: e.target.parentElement.children[e.target.parentElement.children.length - 1].innerText, Name: e.target.parentElement.children[0].innerText})
		}
	}
	handleClickSelectButton = () => {
		var {setSelectIdPeople} = this.props;
		if (this.state.id) {
			setSelectIdPeople({Id: this.state.id, Name: this.state.Name});
			this.handleClickClose()
		}

	}

	render(){
		console.log(this.props)
		const {items} = this.props.modal;
		return(
			<div>
				<div ref="overlay" className={s.overlay} onClick={this.handleClickClose}></div>
				<div ref="popup" className={s.popup}>
					<div className={s.modalBody}>
						<div className={s.topMenu}>
							<div className={s.modalTitle}><div>Ответственные лица</div></div>
							<div className={s.closeButton} onClick={this.handleClickClose}><div></div></div>
						</div>
						<div className={s.table}>
							<div className={s.tTitle}>
								<div>Наименование</div>
								<div>Год рождения</div>
								<div>Адрес</div>
								<div>Серия</div>
								<div>Номер</div>
								<div>Телефон</div>
								<div>Должность</div>
							</div>
							<div className={s.tBody}>
								{(function(){
									return items.map((item, i) => {
										return <div className={s.tBodyItems} onClick={this.handleClickTBodyItem} key={i}>
											<div>{item.Name}</div>
											<div>{item.Year}</div>
											<div>{item.Address}</div>
											<div>{item.Ep}</div>
											<div>{item.Num}</div>
											<div>{item.Tel}</div>
											<div>{item.Position}</div>
											<div>{item.Id}</div>
										</div>
									})
								}.bind(this))()}
							</div>
						</div>
						<div className={s.bottomMenu}>
							<button onClick={this.handleClickSelectButton}>Выбрать</button>
						</div>
					</div>
				</div>
			</div>
		)
	}
}

export default PeopleModalDisplay;
