import React, {Component} from 'react';
import s from './Arrival.module.css'
import ReactToPrint from "react-to-print";

import TovarsModalDisplay from '../../ModalContainers/Tovars'
import PeopleModalDisplay from '../../ModalContainers/People'
import ProvidersModalDisplay from '../../ModalContainers/Providers'


class Arrival extends Component{

	constructor(props) {
		super(props);
		this.state = {
			id: 0,
			Tovars: '',
			People: '',
			Provider: '',
			items: [],
			Number: '',
			Date: ''
		};
		this.handleClickBodyItems = this.handleClickBodyItems.bind(this)
	}

	componentWillMount() {
		if (!this.props.items.flag) {
			this.props.socket.emit('getInformation', 'Arrival', localStorage.Id);
		}
		this.props.socket.on('resInformation', (data, obj, Doc) => {
			this.setState({items: data, Number: Doc.Number, Date: Doc.Date})
			const { setItems } = this.props;
			setItems({obj ,flag: true})

		});
	}
	componentDidMount() {
		this.props.setSelectIdTovars({Id: 0, Name: ''})
		this.props.setSelectIdPeople({Id: 0, Name: ''})
		this.props.setSelectIdProviders({Id: 0, Name: ''})
	}
	componentWillReceiveProps(nextProps) {
		if (nextProps.items.tovars.Id) {
			this.setState({Tovars: nextProps.items.tovars.Name})
		}
		if (nextProps.items.people.Id) {
			this.setState({People: nextProps.items.people.Name})
		}
		if (nextProps.items.providers.Id){
			this.setState({Provider: nextProps.items.providers.Name})
		}
	}
	handleClickBodyItems = (e) => {
		e.persist() 
		var i, j;
		if(e.target.parentElement.className === s.tBody) {
			for (i = 0; i < e.target.parentElement.children.length; i++) {
				for (j = 0; j < e.target.parentElement.children[i].children.length; j++) {
					e.target.parentElement.children[i].children[j].classList.remove(s.active);
				}
			}
		} else {
			for (i = 0; i < e.target.parentElement.parentElement.children.length; i++) {
				for (j = 0; j < e.target.parentElement.parentElement.children[i].children.length; j++) {
					e.target.parentElement.parentElement.children[i].children[j].classList.remove(s.active);
				}
			}
		}
		if (e.target.className === s.tBodyItems) {
			for (i = 0; i < e.target.children.length; i++) {
				e.target.children[i].className = s.active;
			}
			this.setState({id: e.target.children[e.target.children.length - 1].children[0].innerText})
		} else {
			for (i = 0; i < e.target.parentElement.children.length; i++) {
				e.target.parentElement.children[i].className = s.active;
			}
			this.setState({id: e.target.parentElement.children[e.target.parentElement.children.length - 1].children[0].innerText})
		}
	}
	handleSubmitAdd = (e) =>{
		e.preventDefault();
		e.persist() 

		let data = {
			TovarId: this.props.items.tovars.Id,
			Quantity: this.refs.Quantity.value,
			DateId: this.state.Number
		}

		this.props.socket.emit('insertInTo', 'Arrival', data);

	}
	handleClickSend = () => {
		var data = [];
		for (var i = 0; i < this.refs.tBody.children.length; i++) {
			data.push({
				Id : this.refs.tBody.children[i].children[this.refs.tBody.children[i].children.length - 1].children[1].innerText,
				Quantity: this.refs.tBody.children[i].children[4].innerText,
				Count: this.refs.tBody.children[i].children[5].innerText
			})
		}
		this.props.socket.emit('insertInTo', 'StorageFromArrival', {data, people: this.props.items.people.Id, Provider: this.props.items.providers.Id, DateId: this.state.Number});
			console.log(data)

	}
	handleClickDelete = () => {
		this.props.socket.emit('deleteFrom', 'Arrival', {id: this.state.id, DateId: this.state.Number});
	}

	openModalDisplayTovar = () => {
		this.props.socket.emit('getInformationModal', 'Tovars');
		this.props.setOpenModalTovars({flag: true});
	}

	openModalDisplayPeople = () => {
		this.props.socket.emit('getInformationModal', 'People');
		this.props.setOpenModalPeople({flag: true});
	}

	openModalDisplayProvider = () => {
		this.props.socket.emit('getInformationModal', 'Providers');
		this.props.setOpenModalProviders({flag: true});
	}

	handleChange = () => {
	}
	render(){
		return(
			<div>
				<div ref={el => (this.componentRef = el)} className={s.table}>
					<div className={s.bottomPrint}>
						<div>Приходная накладная № {this.state.Number}</div>
					</div>
					<div className={s.tTitle}>
						<div>Артикул</div>
						<div>Наименование</div>
						<div>Цена</div>
						<div>Ед. изм</div>
						<div>Кол-во</div>
						<div>Итого</div>
					</div>
					<div ref="tBody" className={s.tBody}>
						{this.props.items.obj === 'Arrival' ? (function(){
							return this.state.items.map((item, i) => {
								let count = ''
								item.Quantity && item.Price ? count = item.Quantity * item.Price
								: console.log()
								return <div className={s.tBodyItems} onClick={this.handleClickBodyItems} key={i}>
									<div>{item.Number}</div>
									<div>{item.Name}</div>
									<div>{item.Price}</div>
									<div>{item.Unit}</div>
									<div>{item.Quantity}</div>
									<div>{count}</div>
									<div><div>{item.Id}</div><div>{item.IdT}</div></div>
								</div>
							})
						}.bind(this))() : 'Загрузка...'}
					</div>
					<div className={s.bottomPrint}>
						<div>Сформировал: {localStorage.Name}</div>
						<div>Ответственно лицо: {this.state.People}</div>
						<div>Поставщик: {this.state.Provider}</div>
					</div>
				</div>
				<div className={s.editPanel}>
					<form onSubmit={this.handleSubmitAdd}>
						<div className={s.line}>
							<div>
								<input ref="Tovars" onChange={this.handleChange} type="text" placeholder="Товар" value={this.state.Tovars}/>
								<button type="button" onClick={this.openModalDisplayTovar}></button>
							</div>
							<ReactToPrint
								trigger={() => <button className={s.buttonPrint} type="button">Печать</button>}
								content={() => this.componentRef}
							/>
							<div className={s.dateSpan}>
								<span>Дата: {this.state.Date}</span>
							</div>
						</div>
						<div className={s.line}>
							<input ref="Quantity" type="text" placeholder="Единица измерения" />
							<div>
								<input ref="People" type="text" onChange={this.handleChange} value={this.state.People} placeholder="Ответственное лицо" />
								<button type="button" onClick={this.openModalDisplayPeople}></button>
							</div>
							<div>
								<input ref="Provider" type="text" onChange={this.handleChange} value={this.state.Provider} placeholder="Поставщик" />
								<button type="button" onClick={this.openModalDisplayProvider}></button>
							</div>
							<div className={s.dateSpan}>
								<span>Номер: {this.state.Number}</span>
							</div>
						</div>
						<div className={s.lineButtons}>
							<button type="submit">Добавить</button>
							<button type="button" onClick={this.handleClickDelete}>Удалить</button>
							<button type="button" onClick={this.handleClickSend}>Отправить</button>
						</div>
					</form>
				</div>

				{this.props.modal.openModalTovars ? <TovarsModalDisplay showModal={this.props.modal.openModalTovars} socket={this.props.socket}/> : null}
				{this.props.modal.openModalPeople ? <PeopleModalDisplay showModal={this.props.modal.openModalPeople} socket={this.props.socket}/> : null}
				{this.props.modal.openModalProviders ? <ProvidersModalDisplay showModal={this.props.modal.openModalProviders} socket={this.props.socket}/> : null}
			</div>
		)
	}
}

export default Arrival;
