import React, {Component} from 'react';
import s from './People.module.css'


class People extends Component{

	constructor(props) {
		super(props);
		this.state = {
			Id: 0,
			items: []
		};
	}
	componentWillMount() {
		if (!this.props.items.flag) {
			this.props.socket.emit('getInformation', 'People');
		}
		this.props.socket.on('resInformationPeople', (data, obj) => {
			const { setItems } = this.props;
			this.setState({items: data})
			setItems({obj ,flag: true})
		});
	}
	componentWillUnmount() {
		this.props.socket.removeListener('resInformationPeople')
	}
	handleClickBodyItems = (e) => {
		e.persist()
		var i, j;
		if(e.target.parentElement.className === s.tBody) {
			for (i = 0; i < e.target.parentElement.children.length; i++) {
				for (j = 0; j < e.target.parentElement.children[i].children.length; j++) {
					e.target.parentElement.children[i].children[j].classList.remove(s.active);
				}
			}
		} else {
			for (i = 0; i < e.target.parentElement.parentElement.children.length; i++) {
				for (j = 0; j < e.target.parentElement.parentElement.children[i].children.length; j++) {
					e.target.parentElement.parentElement.children[i].children[j].classList.remove(s.active);
				}
			}
		}
		if (e.target.className === s.tBodyItems) {
			for (i = 0; i < e.target.children.length; i++) {
				e.target.children[i].className = s.active;
				this.setState({Id: e.target.children[e.target.children.length - 1].innerText})
			}
		} else {
			for (i = 0; i < e.target.parentElement.children.length; i++) {
				e.target.parentElement.children[i].className = s.active;
				this.setState({Id: e.target.parentElement.children[e.target.parentElement.children.length - 1].innerText})
			}
		}
	}

	handleSubmitAdd = (e) => {
		e.preventDefault();
		let refs = this.refs;
		let data = {
			Name: refs.Name.value,
			Address: refs.Address.value,
			Year: refs.Year.value,
			Ep: refs.Ep.value,
			Num: refs.Num.value,
			Tel: refs.Tel.value,
			Position: refs.Position.value
		}
		if (data.Name && data.Address && data.Year && data.Ep && data.Num && data.Tel && data.Position){

			this.props.socket.emit('insertInTo','People' ,data);
		}
	}

	handleClickDelete = () => {
		if (this.state.Id) {
			this.props.socket.emit('deleteFrom' ,'People', this.state.Id);
		}
	}

	handleClickUpdate = () => {
		let refs = this.refs;
		let data = {
			Name: refs.Name.value,
			Address: refs.Address.value,
			Year: refs.Year.value,
			Ep: refs.Ep.value,
			Num: refs.Num.value,
			Tel: refs.Tel.value,
			Position: refs.Position.value,
			Id: this.state.Id
		}
		console.log(data)

		if (data.Name && data.Address && data.Year && data.Ep && data.Num && data.Tel && data.Position && data.Id){
		console.log(data)

			this.props.socket.emit('update', 'People', data)
		}
	}
	render(){
		return(
			<div>
				<div className={s.table}>
					<div className={s.tTitle}>
						<div>ФИО</div>
						<div>Адрес</div>
						<div>Год рождения</div>
						<div>Серия</div>
						<div>Номер</div>
						<div>Телефон</div>
						<div>Должность</div>
					</div>
					<div className={s.tBody}>
						{this.props.items.obj === 'People' ? (function(){
							return this.state.items.map((item, i) => {
								return <div className={s.tBodyItems} onClick={this.handleClickBodyItems} key={i}>
									<div>{item.Name}</div>
									<div>{item.Address}</div>
									<div>{item.Year}</div>
									<div>{item.Ep}</div>
									<div>{item.Num}</div>
									<div>{item.Tel}</div>
									<div>{item.Position}</div>
									<div>{item.Id}</div>
								</div>
							})
						}.bind(this))() : 'Загрузка...'}
					</div>
				</div>
				<div className={s.editPanel}>
					<form onSubmit={this.handleSubmitAdd}>
						<div className={s.line}>
							<input ref="Name" type="text" placeholder="ФИО" />
							<input ref="Address" type="text" placeholder="Адрес" />
							<input ref="Year" type="text" placeholder="Год рождения" />
						</div>
						<div className={s.line}>
							<input ref="Ep" type="text" placeholder="Серия" />
							<input ref="Num" type="text" placeholder="Номер" />
							<input ref="Tel" type="text" placeholder="Телефон" />
							<input ref="Position" type="text" placeholder="Должность" />
						</div>
						<div className={s.lineButtons}>
							<button type="submit">Добавить</button>
							<button type="button" onClick={this.handleClickDelete}>Удалить</button>
							<button type="button" onClick={this.handleClickUpdate}>Обновить</button>
						</div>
					</form>
				</div>
			</div>
		)
	}
}

export default People;
