import React, {Component} from 'react';
import s from './Type.module.css'


class Type extends Component{

	constructor(props) {
		super(props);
		this.state = {
			Id: 0,
			items: []
		};
		this.handleClick = this.handleClick.bind(this);
	}
	componentWillMount() {
		if (!this.props.items.flag) {
			this.props.socket.emit('getInformation', 'Type');
		}
		this.props.socket.on('resInformationType', (data, obj) => {
			const { setItems } = this.props;
			this.setState({items: data})
			setItems({obj ,flag: true})
		});
	}
	componentWillUnmount() {
		this.props.socket.removeListener('resInformationType')
	}
	handleClick = (e) => {
		e.persist() 
		var i, j;
		if(e.target.parentElement.className === s.tBody) {
			for (i = 0; i < e.target.parentElement.children.length; i++) {
				for (j = 0; j < e.target.parentElement.children[i].children.length; j++) {
					e.target.parentElement.children[i].children[j].classList.remove(s.active);
				}
			}
		} else {
			for (i = 0; i < e.target.parentElement.parentElement.children.length; i++) {
				for (j = 0; j < e.target.parentElement.parentElement.children[i].children.length; j++) {
					e.target.parentElement.parentElement.children[i].children[j].classList.remove(s.active);
				}
			}
		}
		if (e.target.className === s.tBodyItems) {
			for (i = 0; i < e.target.children.length; i++) {
				e.target.children[i].className = s.active;
				this.setState({Id: e.target.children[0].innerText})
			}
		} else {
			for (i = 0; i < e.target.parentElement.children.length; i++) {
				e.target.parentElement.children[i].className = s.active;
				this.setState({Id: e.target.parentElement.children[0].innerText})
			}
		}
	}

	handleSubmitAdd = (e) => {
		e.preventDefault();
		var data = {
			Name: this.refs.Name.value,
		}
		if (data.Name){
			this.props.socket.emit('insertInTo','Type' ,data);
		}
	}

	handleClickDelete = () => {
		if (this.state.Id) {
			this.props.socket.emit('deleteFrom' ,'Type', this.state.Id);
		}
	}

	handleClickUpdate = () => {
		var data = {
			Name: this.refs.Name.value,
			Id: this.state.Id
		}
		if (data.Name && data.Id){
			this.props.socket.emit('update', 'Type', data)
		}
	}

	render() {
		return(
			<div className={s.main}>
				<div className={s.table}>
					<div className={s.tTitle}>
						<div>Номер</div>
						<div>Наименование</div>
					</div>
					<div className={s.tBody}>
						{this.props.items.obj === 'Type' ? (function(){
							return this.state.items.map((item, i) => {
								return <div className={s.tBodyItems} onClick={this.handleClick} key={i}>
									<div>{item.Id}</div>
									<div>{item.Name}</div>
								</div>
							})
						}.bind(this))() : 'Загрузка...'}
					</div>
				</div>
				<div className={s.editPanel}>
					<form onSubmit={this.handleSubmitAdd}>
						<div className={s.line}>
							<input ref="Name" type="text" placeholder="Наименование" />
						</div>
						<div className={s.lineButtons}>
							<button type="submit">Добавить</button>
							<button type="button" onClick={this.handleClickDelete}>Удалить</button>
							<button type="button" onClick={this.handleClickUpdate}>Обновить</button>
						</div>
					</form>
				</div>
			</div>
		)
	}
}

export default Type;
