import React, {Component} from 'react';
import s from './Real.module.css'


class Real extends Component{

	constructor(props) {
		super(props);
		this.state = {
			idAcc: 0,
			idDreal: 0,
			manufacturer: '',
			type: '',
			real: [],
			account: [],
			dreal: []
		};
		this.handleClickBodyItems = this.handleClickBodyItems.bind(this)
	}
	componentWillMount() {
		if (!this.props.items.flag) {
			this.props.socket.emit('getInformation', 'StatisticsReal');
		}
		this.props.socket.on('resInformationStatisticsReal', (data, obj) => {
			this.setState({real: data.res, account: data.account, dreal: data.dreal})
			const { setItems } = this.props;
			setItems({obj ,flag: true})
		});
		this.props.socket.on('resInformationSelectDreal', (data) => {
			this.setState({real: data.res, dreal: data.dreal})
			const { setItems } = this.props;
			setItems({obj:'StatisticsReal' ,flag: true})
		});

		this.props.socket.on('resInformationSelectReal', (res) => {
			console.log(res)
			this.setState({real: res})
			const { setItems } = this.props;
			setItems({obj:'StatisticsReal' ,flag: true})
		});
	}
	componentWillUnmount() {
		this.props.socket.removeListener('resInformationStatisticsReal')
		this.props.socket.removeListener('resInformationSelectDreal')
		this.props.socket.removeListener('resInformationSelectReal')

	}

	handleClickBodyItems = (e) => {
		e.persist() 
		var i, j;
		if(e.target.parentElement.className === s.tBody) {
			for (i = 0; i < e.target.parentElement.children.length; i++) {
				for (j = 0; j < e.target.parentElement.children[i].children.length; j++) {
					e.target.parentElement.children[i].children[j].classList.remove(s.active);
				}
			}
		} else {
			for (i = 0; i < e.target.parentElement.parentElement.children.length; i++) {
				for (j = 0; j < e.target.parentElement.parentElement.children[i].children.length; j++) {
					e.target.parentElement.parentElement.children[i].children[j].classList.remove(s.active);
				}
			}
		}
		if (e.target.className === s.tBodyItems) {
			for (i = 0; i < e.target.children.length; i++) {
				e.target.children[i].className = s.active;
			}
		} else {
			for (i = 0; i < e.target.parentElement.children.length; i++) {
				e.target.parentElement.children[i].className = s.active;
			}
		}
	}
	handleClickBodyItemsAccount = (e) => {
		e.persist() 
		if (e.target.className === s.tBodyItems) {
			this.setState({idAcc: e.target.children[e.target.children.length - 1].innerText})
			this.props.socket.emit('selectDreal', e.target.children[e.target.children.length - 1].innerText);
		} else {
			this.setState({idAcc: e.target.parentElement.children[e.target.parentElement.children.length - 1].innerText})
			this.props.socket.emit('selectDreal', e.target.parentElement.children[e.target.parentElement.children.length - 1].innerText);
		}

	}
	handleClickBodyItemsDreal = (e) => {
		e.persist() 
		if (e.target.className === s.tBodyItems) {
			this.setState({idDarr: e.target.children[e.target.children.length - 1].innerText})
			this.props.socket.emit('selectReal', e.target.children[e.target.children.length - 1].innerText)
						console.log(e.target.children[e.target.children.length - 1].innerText)
		} else {
			this.setState({idDarr: e.target.parentElement.children[e.target.parentElement.children.length - 1].innerText})
			this.props.socket.emit('selectReal', e.target.parentElement.children[e.target.parentElement.children.length - 1].innerText)
			console.log(e.target.parentElement.children[e.target.parentElement.children.length - 1].innerText)
		}
	}

	handleChange = () => {
		console.log('tnk')
	}
	render(){
		console.log(this.state)
		return(
			<div className={s.mainFlex}>
				<div className={s.topFlex}>
					<div>
						<div className={s.table}>
							<div className={s.tTitle}>
								<div>ФИО</div>
							</div>
							<div className={s.tBody}>
								{this.props.items.obj === 'StatisticsReal' ? (function(){
									return this.state.account.map((item, i) => {
										return <div className={s.tBodyItems} onClick={(e) => {this.handleClickBodyItems(e); this.handleClickBodyItemsAccount(e)}} key={i}>
											<div>{item.Name}</div>
											<div>{item.Id}</div>
										</div>
									})
								}.bind(this))() : 'Загрузка...'}
							</div>
						</div>
					</div>
					<div>
						<div className={s.table}>
							<div className={s.tTitle}>
								<div>Дата</div>
								<div>Ответственное лицо</div>
							</div>
							<div className={s.tBody}>
								{this.props.items.obj === 'StatisticsReal' ? (function(){
									return this.state.dreal.map((item, i) => {
											return <div className={s.tBodyItems} onClick={(e) => {this.handleClickBodyItems(e); this.handleClickBodyItemsDreal(e)}} key={i}>
												<div>{item.Date}</div>
												<div>{item.Name}</div>
												<div>{item.Id}</div>
											</div>
										})
								}.bind(this))() : 'Загрузка...'}
							</div>
						</div>
					</div>
				</div>
				<div className={s.arrival}>
					<div className={s.table}>
						<div className={s.tTitle}>
							<div>Артикул</div>
							<div>Наименование</div>
							<div>Цена</div>
							<div>Ед. изм</div>
							<div>Кол-во</div>
							<div>Итого</div>
						</div>
						<div className={s.tBody}>
							{this.props.items.obj === 'StatisticsReal' ? (function(){
								return this.state.real.map((item, i) => {
										let count = ''
										item.Quantity && item.Price ? count = item.Quantity * item.Price
										: console.log()
										return <div className={s.tBodyItems} key={i}>
											<div>{item.Number}</div>
											<div>{item.Name}</div>
											<div>{item.Price}</div>
											<div>{item.Unit}</div>
											<div>{item.Quantity}</div>
											<div>{count}</div>
											<div><div>{item.Id}</div><div>{item.IdT}</div></div>
										</div>
									})
							}.bind(this))() : 'Загрузка...'}
						</div>
					</div>
				</div>
				<div className={s.lineButtons}>
					<button type="button" onClick={this.handleClickPrint}>Печать</button>
				</div>
			</div>
		)
	}
}

export default Real;
