import React, {Component} from 'react';
import ReactDOM from "react-dom";
import s from './Statistics.module.css'
import { RangeDatePicker } from "@y0c/react-datepicker";
import ReactToPrint from "react-to-print";
import '@y0c/react-datepicker/assets/styles/calendar.scss';

import "dayjs/locale/ru";

class Statistics extends Component{

	constructor(props) {
		super(props);
		this.state = {
			id: 0,
			startDate: '',
			endDate: '',
			items: []
		};
	}
	componentWillMount() {
		this.props.socket.on('resStatistics', (data) => {
			console.log(data)
			this.setState({items: data})
			const { setItems } = this.props;
			setItems({obj: 'Statistics' ,flag: true})
		});

	}
	componentWillUnmount() {
		this.props.socket.removeListener('resStatistics')
	}

	getDateNow = (date) => {
		var day = date.getDate();
		if (day<10) day='0'+day; 
		var month = date.getMonth() + 1;
		if (month<10) month='0'+month; 
		var year = date.getFullYear(); 
		return day + '.' + month + '.' + year; 
	}
	datePickerChange = (...args) => {
		this.setState({startDate: this.getDateNow(args[0]), endDate: args[1] && this.getDateNow(args[1])})
	}

	handleClickSelect = () => {

		var per = this.state.startDate;
		var min = per[3] + per[4] + per[0] + per[1];
		per = this.state.endDate;
		var max = per[3] + per[4] + per[0] + per[1];

		this.props.socket.emit('statistic',min, max);
		console.log(min + ' ' + max)
	}
	render(){
		return(
			<div>
				<div className={s.editPanel}>
					<div className={s.datePicker}>
						<RangeDatePicker locale='ru' onChange={this.datePickerChange} />
					</div>
					<div className={s.panelButton}>
						<button type="button" onClick={this.handleClickSelect}>Сформировать</button>
						<ReactToPrint
							trigger={() => <button type="button" onClick={this.handleClickPrint}>Отчет</button>}
							content={() => this.componentRef}
						/>
					</div>
				</div>
				<div ref={el => (this.componentRef = el)} className={s.table}>
					<div className={s.bottomPrint}>
						<div>Отчет статистики от: {this.state.startDate} по: {this.state.endDate}</div>
					</div>
					<div className={s.tTitle}>
						<div>Артикул</div>
						<div>Наименование</div>
						<div>Цена</div>
						<div>Ед. изм</div>
						<div>Приход</div>
						<div>Расход</div>
						<div>Остаток</div>
					</div>
					<div className={s.tBody}>
						{this.props.items.obj === 'Statistics' ? (function(){
							return this.state.items.map((item, i) => {
								if (item.residue > 0) { return <div className={s.tBodyItems} onClick={this.handleClickBodyItems} key={i}>
									<div>{item.Number}</div>
									<div>{item.Name}</div>
									<div>{item.Price}</div>
									<div>{item.Unit}</div>
									<div className={s.qa}>{item.QA}</div>
									<div className={s.qr}>{item.QR}</div>
									<div className={s.qa}>{Math.abs(item.residue)}</div>
									<div>{item.Id}</div>
								</div>}
								else { return <div className={s.tBodyItems} onClick={this.handleClickBodyItems} key={i}>
									<div>{item.Number}</div>
									<div>{item.Name}</div>
									<div>{item.Price}</div>
									<div>{item.Unit}</div>
									<div className={s.qa}>{item.QA}</div>
									<div className={s.qr}>{item.QR}</div>
									<div className={s.qr}>{Math.abs(item.residue)}</div>
									<div>{item.Id}</div>
								</div>}
							})
						}.bind(this))() : 'Выберите период времени'}
					</div>
				<div className={s.bottomPrint}>
					<div>Сформировал: {localStorage.Name}</div>
				</div>
				</div>
			</div>
		)
	}
}

export default Statistics;
