import React, {Component} from 'react';
import s from './Arrival.module.css'


class Arrival extends Component{

	constructor(props) {
		super(props);
		this.state = {
			idAcc: 0,
			idDarr: 0,
			manufacturer: '',
			type: '',
			arrival: [],
			account: [],
			darr: []
		};
		this.handleClickBodyItems = this.handleClickBodyItems.bind(this)
	}
	componentWillMount() {
		if (!this.props.items.flag) {
			this.props.socket.emit('getInformation', 'StatisticsArrival');
			console.log('text')
		}
		this.props.socket.on('resInformationStatisticsArrival', (data, obj) => {
			console.log(data)
			this.setState({arrival: data.res, account: data.account, darr: data.darr})
			const { setItems } = this.props;
			setItems({obj ,flag: true})
		});
		this.props.socket.on('resInformationSelectDarr', (data) => {
			console.log(data, '111111111111111111111')
			this.setState({arrival: data.res, darr: data.darr})
			const { setItems } = this.props;
			setItems({obj:'StatisticsArrival' ,flag: true})
		});

		this.props.socket.on('resInformationSelectArrival', (res) => {
			this.setState({arrival: res})
			const { setItems } = this.props;
			setItems({obj:'StatisticsArrival' ,flag: true})
		});
	}
	componentWillUnmount() {
		this.props.socket.removeListener('resInformationStatisticsArrival')
	}
	componentDidMount() {
		this.props.setSelectIdManufacturer({Id: 0, Name: ''})
		this.props.setSelectIdType({Id: 0, Name: ''})
	}


	componentWillReceiveProps(nextProps) {
		console.log(nextProps)
		if (nextProps.items.type.Id) {
			this.setState({type: nextProps.items.type.Name})
		}
		if (nextProps.items.manufacturer.Id) {
			this.setState({manufacturer: nextProps.items.manufacturer.Name})
		}
	}

	handleClickBodyItems = (e) => {
		e.persist() 
		var i, j;
		if(e.target.parentElement.className === s.tBody) {
			for (i = 0; i < e.target.parentElement.children.length; i++) {
				for (j = 0; j < e.target.parentElement.children[i].children.length; j++) {
					e.target.parentElement.children[i].children[j].classList.remove(s.active);
				}
			}
		} else {
			for (i = 0; i < e.target.parentElement.parentElement.children.length; i++) {
				for (j = 0; j < e.target.parentElement.parentElement.children[i].children.length; j++) {
					e.target.parentElement.parentElement.children[i].children[j].classList.remove(s.active);
				}
			}
		}
		if (e.target.className === s.tBodyItems) {
			for (i = 0; i < e.target.children.length; i++) {
				e.target.children[i].className = s.active;
			}
		} else {
			for (i = 0; i < e.target.parentElement.children.length; i++) {
				e.target.parentElement.children[i].className = s.active;
			}
		}
	}
	handleClickBodyItemsAccount = (e) => {
		e.persist() 
		if (e.target.className === s.tBodyItems) {
			this.setState({idAcc: e.target.children[e.target.children.length - 1].innerText})
			this.props.socket.emit('selectDarr', e.target.children[e.target.children.length - 1].innerText);
		} else {
			this.setState({idAcc: e.target.parentElement.children[e.target.parentElement.children.length - 1].innerText})
			this.props.socket.emit('selectDarr', e.target.parentElement.children[e.target.parentElement.children.length - 1].innerText);
		}

	}
	handleClickBodyItemsDarr = (e) => {
		e.persist() 
		if (e.target.className === s.tBodyItems) {
			this.setState({idDarr: e.target.children[e.target.children.length - 1].innerText})
			this.props.socket.emit('selectArrival', e.target.children[e.target.children.length - 1].innerText)
		} else {
			this.setState({idDarr: e.target.parentElement.children[e.target.parentElement.children.length - 1].innerText})
			this.props.socket.emit('selectArrival', e.target.parentElement.children[e.target.parentElement.children.length - 1].innerText)
		}
	}
	handleClickSubmitAdd = (e) =>{
		e.preventDefault();
		e.persist() 

		let refs = this.refs

		let data = {
			Number: refs.Number.value,
			Name: refs.Name.value,
			Price: refs.Price.value,
			Unit: refs.Unit.value,
			ManufacturerId: this.props.items.manufacturer.Id,
			TypeId: this.props.items.type.Id
		}
		this.props.socket.emit('insertInTo', 'Tovars', data);

	}
	handleClickUpdate = () => {
		let refs = this.refs

		let data = {
			Number: refs.Number.value,
			Name: refs.Name.value,
			Price: refs.Price.value,
			Unit: refs.Unit.value,
			ManufacturerId: this.props.items.manufacturer.Id,
			TypeId: this.props.items.type.Id,
			Id: this.state.id
		}

		this.props.socket.emit('update', 'Tovars', data);

	}
	handleClickDelete = () => {
		this.props.socket.emit('deleteFrom', 'Tovars', this.state.id);
	}

	openModalDisplayManufacturers = () => {
		this.props.socket.emit('getInformationModal', 'Manufacturers');
		this.props.setOpenModalManufacturer({flag: true});
	}

	openModalDisplayType = () => {
		this.props.socket.emit('getInformationModal', 'Type');
		this.props.setOpenModalType({flag: true});
	}
	handleChange = () => {
		console.log('tnk')
	}
	render(){
		return(
			<div className={s.mainFlex}>
				<div className={s.topFlex}>
					<div>
						<div className={s.table}>
							<div className={s.tTitle}>
								<div>ФИО</div>
							</div>
							<div className={s.tBody}>
								{this.props.items.obj === 'StatisticsArrival' ? (function(){
									return this.state.account.map((item, i) => {
										return <div className={s.tBodyItems} onClick={(e) => {this.handleClickBodyItems(e); this.handleClickBodyItemsAccount(e)}} key={i}>
											<div>{item.Name}</div>
											<div>{item.Id}</div>
										</div>
									})
								}.bind(this))() : 'Загрузка...'}
							</div>
						</div>
					</div>
					<div>
						<div className={s.table}>
							<div className={s.tTitle}>
								<div>Дата</div>
								<div>Ответственное лицо</div>
								<div>Поставщик</div>
							</div>
							<div className={s.tBody}>
								{this.props.items.obj === 'StatisticsArrival' ? (function(){
									return this.state.darr.map((item, i) => {
											return <div className={s.tBodyItems} onClick={(e) => {this.handleClickBodyItems(e); this.handleClickBodyItemsDarr(e)}} key={i}>
												<div>{item.Date}</div>
												<div>{item.NamePeo}</div>
												<div>{item.NamePro}</div>
												<div>{item.Id}</div>
											</div>
										})
								}.bind(this))() : 'Загрузка...'}
							</div>
						</div>
					</div>
				</div>
				<div className={s.arrival}>
					<div className={s.table}>
						<div className={s.tTitle}>
							<div>Артикул</div>
							<div>Наименование</div>
							<div>Цена</div>
							<div>Ед. изм</div>
							<div>Кол-во</div>
							<div>Итого</div>
						</div>
						<div className={s.tBody}>
							{this.props.items.obj === 'StatisticsArrival' ? (function(){
								return this.state.arrival.map((item, i) => {
										let count = ''
										item.Quantity && item.Price ? count = item.Quantity * item.Price
										: console.log()
										return <div className={s.tBodyItems} key={i}>
											<div>{item.Number}</div>
											<div>{item.Name}</div>
											<div>{item.Price}</div>
											<div>{item.Unit}</div>
											<div>{item.Quantity}</div>
											<div>{count}</div>
											<div><div>{item.Id}</div><div>{item.IdT}</div></div>
										</div>
									})
							}.bind(this))() : 'Загрузка...'}
						</div>
					</div>
				</div>
				<div className={s.lineButtons}>
					<button type="button" onClick={this.handleClickPrint}>Печать</button>
				</div>
			</div>
		)
	}
}

export default Arrival;
