import React, {Component} from 'react';
import s from './Menu.module.css'
import {Link} from 'react-router-dom'


class Menu extends Component{
	handleClick = (obj,text) => {
		this.props.socket.emit('getInformation',obj);
	}
	render(){
		return(
			<ul className={s.mainUl}>
				<li>
					<div>Справочниуи</div>
					<ul className={s.subUl}>
						<li><Link to='/app/tovars'><div onClick={this.handleClick.bind(this, 'Tovars')}>Товары</div></Link></li>
						<li><Link to='/app/manufacturers'><div onClick={this.handleClick.bind(this, 'Manufacturers')}>Производители</div></Link></li>
						<li><Link to='/app/type'><div onClick={this.handleClick.bind(this, 'Type')}>Категории</div></Link></li>
						<li><Link to='/app/providers'><div onClick={this.handleClick.bind(this, 'Providers')}>Поставщики</div></Link></li>
						<li><Link to='/app/people'><div onClick={this.handleClick.bind(this, 'People')}>Ответственные лица</div></Link></li>
					</ul>
				</li>
				<li>
					<Link to='/app/storage'>
						<div onClick={this.handleClick.bind(this, 'Storage')}>
							Склад
						</div>
					</Link>
				</li>
				<li>
					<div>Статистика</div>
					<ul className={s.subUl}>
						<li><Link to='/app/statistics/arrival'><div onClick={this.handleClick.bind(this, 'StatisticsArrival')}>Статистика прихода</div></Link></li>
						<li><Link to='/app/statistics/real'><div onClick={this.handleClick.bind(this, 'StatisticsReal')}>Статистика расхода</div></Link></li>
						<li><Link to='/app/statistics'><div>Статистика за период</div></Link></li>
					</ul>
				</li>
				<li><div>Portfolio</div></li>
				<li><div>Contact</div></li>
			</ul>
		)
	}
}

export default Menu;
