import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as actions from '../../Actions/modal';
import * as setSelectIdActions from '../../Actions/items'
import People from '../ModalComponents/People/People';


const mapStateProps = ({modal}) => ({
	modal
})

const mapDispatchToProps = dispatch => ({
	...bindActionCreators(actions, dispatch),
	...bindActionCreators(setSelectIdActions, dispatch)
});


export default connect(mapStateProps, mapDispatchToProps)(People);
